import Layout from "@components/layout";
import Seo from "@components/seo";

const Banner = React.lazy(() => import("@screens/main/main_v2/banner"));
const AboutUs = React.lazy(() => import("@screens/main/main_v2/about"));
const Achievement = React.lazy(() => import("@screens/main/main_v2/achievement"));
const OurService = React.lazy(() => import("@screens/main/main_v2/our-service"));
const RecentWorks = React.lazy(() => import("@screens/main/main_v2/recent-works"));
const OurTeam = React.lazy(() => import("@screens/main/main_v2/our-team"));
const MoveForward = React.lazy(() => import("@components/common/move-forward"));
const Faq = React.lazy(() => import("@components/common/faq"));
const OurDevelopment = React.lazy(() => import("@screens/main/main_v2/our-development"));

// const CtaButton = React.lazy(() => import("@components/common/connect-button"));

const IndexPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);

  const faqData = [
    {
      question: "What services does Justtry Technologies offer?",
      answer:
        "Blockchain, DeFi, Wallet, Web2, Web3, Exchange, NFT development.",
    },
    {
      question: "What makes Justtry Technologies stand out?",
      answer:
        "Leading software development company specializing in cutting-edge technologies.",
    },
    {
      question: "Can Justtry Technologies handle diverse projects?",
      answer:
        "Yes, we excel in blockchain, DeFi, wallets, web development, and NFTs.",
    },
    {
      question: "Why choose Justtry Technologies for software development?",
      answer:
        "Expertise in building innovative solutions for blockchain and decentralized technologies.",
    },
  ];

  const { data } = props;
  const fileUrl =
    data?.logo?.childImageSharp?.gatsbyImageData?.images?.fallback?.src;
  const validData = data?.allMetaJson?.nodes[0]

  return (
    <Suspense fallback={""}>
      <Seo
        title={validData?.title}
        metaTitle={validData?.metaTitle}
        description={validData?.description}
        image={fileUrl}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={"/"}
      ></Seo>
      <div className="landing_page">
        <Layout db={db}>
          <Banner db={db} />
          <AboutUs db={db} />
          <Achievement db={db} />
          <OurService db={db} />
          <OurDevelopment />
          {/* <CtaButton db={db} /> */}
          <RecentWorks />
          <OurTeam />
          <Faq faqData={faqData} />
          <MoveForward db={db} />
        </Layout>
      </div>
    </Suspense >
  );
};

export default IndexPage;

