export const queryData = graphql`
  query MyQuery {
    logo: file(relativePath: { eq: "new_logo.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allMetaJson(filter: {slug: {eq: "/"}}) {
      nodes {
        metaTitle
        title
        slug
        description
      }
    }
  }
`;
